.popup {
  display: none;
}

.popup-show {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.5);
}

.popup-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 745px;
  z-index: 101;
}
